.canvas{
    border: 1px solid #000000;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    place-items: center;
    background: #FFFFFF;
    padding: 4%;
}

.canvas-interactive_resourse{
    background: #D9D9D9;
    border-radius: 22px;
    height: 10rem;
    width: 83vw;
    margin: 12% 0 12%;
    text-align: center;
}

.canvas-questions{
    gap: 1.2rem;
    display: grid;
    grid-template-rows: repeat(3,1fr);
}

.inputQuestion{
    margin: 16px 0 0;
    resize: none;
    width: 90%;
    height: 8rem;
    border: none;
    text-align: center;
  }