.modal{
    position: fixed;
    z-index: 999;
    top:0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    background-color: rgba(0,0,0,0.75);
    display: none;
    justify-content: center;
    align-items: center;
}

.is-open{
    display: flex;
}

.modal-container{
    position: relative;
    background-color: aliceblue;
}

.modal-close{
    position: absolute;
    top: -3rem;
    right: 0rem;
}